import {useEffect} from 'react';
import {useInView} from 'react-intersection-observer';
import gsap from 'gsap';
import * as P from './aboutMeDesc.parts';
import {useWindowSize} from '../../../hooks/useWindowSize';

const AboutMeDesc = () => {
    const {ref, inView, entry} = useInView({threshold: 0.4});
    const {height} = useWindowSize();

    useEffect(() => {
        if (inView) {
            gsap.to(entry.target.children, {display: 'block'});
        }
    }, [inView]);
    return (
        <P.Container ref={ref} currentScreenHeight={height}>
            <P.Line>
                Jestem frontend developerem na stanowisku juniorskim z niespena rocznym dowiadczeniem. Na swoim koncie mam dwa projekty
                komercyjne, w tym aplikacj do pozyskiwania danych od uytkownika umoliwiajc wygenerowanie na ich podstawie wniosku o ogoszenie
                upadoci konsumenckiej oraz CRM dla klienta zewntrznego jako narzdzie do sprawnego zarzdzania relacjami z klientami oraz zasobami
                firmy. Projekt by oparty na React z Next, Typescript, Apollo, GraphQL oraz Hasura.
            </P.Line>

            <P.Line>
                W pracy codziennej korzystam z technologii takich jak HTML, CSS, JavaScript, Typescript, React, Bubble co pozwala mi tworzy nowoczesne i responsywne aplikacje. Moje wyksztacenie inynierskie oraz determinacja i cika praca pomagaj mi w deniu do okrelonych celw. Jestem gotw do dalszego rozwoju moich umiejtnoci i wspierania firmy w osiganiu sukcesw. Po pracy tworz wasne projekty oraz uczestnicz w kursach, aby stale rozwija swoje zdolnoci i by cenionym specjalist w brany IT.
            </P.Line>
            {/* <P.Line>
                Jako dynamiczny i ambitny junior frontend developer, pracujcy obecnie z
                frameworkiem React, posiadam wyksztacenie inynierskie oraz ponad proczne
                dowiadczenie komercyjne w brany IT, gdzie obecnie tworz projekty dla klientw
                zewntrznych. Zdobyem szerokie umiejtnoci z zakresu HTML, CSS, JavaScript,
                Typescript, React, dziki samodzielnej nauce przez okoo dwa lata.
            </P.Line>

            <P.Line>
                Aktywnie szukam nowych wyzwa i chtnie podejmuj nowe projekty, dziki ktrym mog
                rozwija swoje umiejtnoci i zdobywa cenne dowiadczenie zawodowe. Moja pasja to
                tworzenie nowoczesnych aplikacji internetowych oraz cigy rozwj moich zdolnoci, a
                determinacja i cika praca s dla mnie kluczem do osigania sukcesw w brany IT i
                bycia cenionym specjalist w swojej dziedzinie.
            </P.Line> */}
        </P.Container>
    );
};

export default AboutMeDesc;
